import React, { Component } from 'react'
import Deposit from './Deposit'
import '../Home.css'

class DepWith extends Component {
  render() {
    const { token, tokenAmt, amtSelected } = this.props
    const DepositWithdraw = (props) => (
      <div className={'depWith'} style={{ alignSelf: 'center', fontSize: '18px', fontWeight: 'bold', padding: '2px 4px' }}>
        {props.txt}
      </div>
    )
    return (
      <div style={{ border: '1px solid #d7dadc', borderRadius: '2px', height: '40vh', width: '30vw' }} className={'deposits'}>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', borderBottom: '1px solid #d7dadc' }}>
          <DepositWithdraw txt={'Deposit'}/>
          <DepositWithdraw txt={'Withdraw'}/>
        </div>
        <Deposit
          token={token}
          tokenAmt={tokenAmt}
          amtSelected={amtSelected}/>
      </div>
    )
  }
}

export default DepWith